.input-wrapper{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: row;
}

.input{
    height:35px;
    outline:none;
    border:1px solid #333;
    border-radius: 5px;
    font-size: 16px;
    padding: 0px 10px 0px 10px;
    width:180px;
}

.add-button{
    height:35px;
    width:50px;
    cursor: pointer;
    margin-left: 15px;
    outline: none;
    color:#fff;
    background-color: #3636c2;
    border:1px solid #3636c2;
    border-radius: 5px;
}