.banner{
    height:fit-content;
    width:300px;
    border: 1px solid #ccc;
    border-radius: 4px;
   
    margin-top:50px;
    padding:15px;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: flex-start;
}

.complete{
    background-color: #b6f5b6;
}

.delete{
    background-color: #e58e8e;
}

.banner-title{
    flex:0.2;
    font-weight: bold;
    text-transform: uppercase;
}

.banner-message{
    flex:0.8;
    margin-left:20px;
}