
li{
    list-style: none;
    width: 250px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border:1px solid #ccc;
    padding: 10px;
    border-radius: 5px;
    margin-top: 10px;
    font-size:16px;
  }

  .checked-text{
    text-decoration: line-through;
  }

  .checked-item{
    background-color: #b7dbb7;

  }

  .delete-button{
    height: 20px;
    width: 20px;
    font-size:20px;
    cursor:pointer;
    background-color: #ec4141;
    color:#fff;

    border:none;
  }

  .mark-as-complete{
    border:none;
    outline: none;
    height: 20px;
    width: 20px;
    cursor:pointer;
  }

  .completed{
    background-color: green;
    color:#fff;
    border: none;
    outline:none;
    height: 20px;
    width: 20px;
    cursor: pointer;
  }