*,*::after,*::before{
  margin:0px;
  padding:0px;
  box-sizing: border-box;
}

*::-webkit-scrollbar{
  width:8px;

}


*::-webkit-scrollbar-thumb{
  background-color: #aaa;
  border-radius: 8px;
}

body{
  height:700px;
  width:100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wrapper{
  padding:20px;
  border: 1px solid #ccc;
  border-radius:8px;
  height:400px;
  width:300px;
  overflow-y: scroll;

}
